.track-timeline-block {
  box-sizing: border-box;
  padding-left: 5px;
  display: block;
  width: 100%;

  margin: 1px 0 1px 0;

  .track-timeline-block-content._fixed {
    position: sticky;
    display: inline-block;
    width: max-content;
    left: 0;
  }

  &._default {
    height: 25px;
    border-top: 1px solid #444;
    border-bottom: 1px solid #444;
    background-color: #222;
  }
  &._description {
    height: 23px;
  }
}